.navbar{
    width: 100%;
    height: 100%;
    background-color:  #121619;
     display: flex;
    flex-direction: row;
    border-bottom: white 300 px; 
}

.navbar .leftSide {
    flex : 50%;
    height: 50%;
    display: flex;
    align-items: center;
    padding-left: 150px;
}
.navbar .leftSide #icon{
    font-size: 90px;
    color: white;
}
.navbar .leftSide h1{
    color: white;
    margin-left: 40px;
    font-size:40px;
    margin-bottom: 0;
}
.navbar .leftSide h2{
    color:darkgray;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-left: 40px;
    margin-top: 0;
}
.navbar .leftSide img {
    width: 20%;
}

.navbar .rightSide{
    flex : 50%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar a{
    color: whitesmoke;
    text-decoration: none;
    margin: 5%;
    font-size: 20px;
}

.navbar .rightSide button{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
}

.navbar .rightSide svg{
    font-size: 40px;
}

.navbar #open{
    padding-left: 0px;
}
.navbar #open img{
    display: none;;
}

.navbar #close img{
    display: inherit;
}

.navbar #open .hiddenLinks{
    display: inherit;
    margin-left: 30px;
}

.navbar #close .hiddenLinks {
    display: none;
}

.navbar #open a{
    width: 70px;
    margin: 5px;
}

@media only screen and (max-width : 900px){
    .navbar .rightSide a{
        width: 70px;
    }
    .navbar .leftSide{
        padding-left: 50px;
    }
}

@media only screen and (max-width: 600px){
    .navbar .rightSide a{
        display: none;
    }
    .navbar .rightSide {
        justify-content: flex-end;
        padding-right: 50px;
    }
    .navbar .rightSide button {
        display: inherit;
    }
}

@media only screen and (min-width : 600px){
    .navbar .rightSide button {
        display: none;
    }

    .hiddenLinks{
        display: none;
    }
}